@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@keyframes headerAction {
  0% {
    opacity: 0;
    transform: translateY(-200px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
header {
  background-color: rgba(255, 255, 255, 0.95);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2000;
  opacity: 0;
  transform: translateY(-200px);
  animation: headerAction 0.6s $ease-out-quad 0.4s forwards;
  .home & {
    animation: headerAction 0.6s $ease-out-quad 0.8s forwards;
  }
  .menuOpen &,
  .headerStyle_01 &,
  .contact & {
    border-bottom: 1px solid $key-gray2;
  }
  .fixedHeader & {
    border-bottom: none;
  }
  > .closeArea {
    display: none;
  }
  > .inner {
    height: $header-size-sp;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .col-logo {
    position: relative;
    width: 70 / $design-num-sp * 100vw;
    z-index: 10;
    img {
      @include imgset;
    }
    a {
      &.current {
        cursor: default;
        pointer-events: none;
      }
    }
  }
  .col-nav {
    display: none;
  }
  .col-menu {
    position: absolute;
    width: 100%;
    display: flex;
    top: 0;
    left: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
  }
  .menuBtn {
    position: relative;
    font-family: $font-dec;
    line-height: (13/26) + em;
    @include fz_vw(26);
    text-align: center;
    top: 1px;
    span {
      font-weight: 400;
      transition: 0.3s;
      text-decoration: underline;
      text-decoration-thickness: 1px;
      &.close {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        width: 100%;
      }
    }
    &.active {
      span {
        &.normal {
          opacity: 0;
        }
        &.close {
          opacity: 1;
        }
      }
    }
  }
  .col-right {
    z-index: 10;
  }
  .col-btn {
    display: none;
  }
  .col-sns {
    ul {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 14 / $design-num-sp * 100vw;
    }
    .instagram {
      width: 15 / $design-num-sp * 100vw;
    }
    .twitter {
      width: 15 / $design-num-sp * 100vw;
    }
    img {
      @include imgset;
      transition: 0.3s;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  header {
    .menuOpen &,
    .headerStyle_01 &,
    .contact & {
      border-bottom: none;
    }
    > .closeArea {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      bottom: 0;
    }
    > .inner {
      height: $header-size;
      justify-content: flex-start;
    }
    .col-logo {
      width: $grid_01/$w_01 * 100%;
      a {
        display: flex;
        width: 89px;
      }
      a:hover {
        transform: translateX(2px);
      }
    }
    .col-nav {
      display: block;
      flex-grow: 1;
    }
    .col-menu {
      display: none;
    }
    .col-right {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      position: relative;
    }
    .col-btn {
      display: block;
      width: 164px;
      height: $header-size;
      display: flex;
      align-items: center;
      button {
        width: 100%;
      }
    }
    .col-sns {
      width: $grid_02 + px;
      ul {
        width: 100%;
        gap: 16px;
      }
      .instagram {
        width: 16px;
      }
      .twitter {
        width: 15px;
      }
      a:hover {
        opacity: 0.9;
        img {
          transform: translateX(2px);
        }
      }
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
  .globalNav {
    display: flex;
    align-items: center;
    // gap: $base-pad;
    .nav-item {
      &.hasChild > a {
        padding-left: $base-pad;
        padding-right: $base-pad;
      }
      &.hasChild.active a {
        .labelIcon {
          rotate: -180deg;
        }
      }
      > a {
        height: $header-size;
        display: flex;
        align-items: center;
        gap: 8px;
        position: relative;
        .labelTx {
          font-family: $font-sub;
          font-weight: 400;
          font-size: 2.4rem;
          line-height: (13/24) + em;
          transition: 0.3s;
        }
        .labelIcon {
          font-weight: 400;
          font-size: 1.2rem;
          line-height: 1em;
          position: relative;
          top: 3px;
          transform-origin: 50% 50%;
          transition: 0.1s;
        }
      }
      > a:hover {
        opacity: 0.9;
        .labelTx {
          transform: translateX(3px);
        }
      }
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
  #hoverBlocker {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 0;
    z-index: 900;
    display: none;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
  .globalChildNav {
    display: none;
    position: absolute;
    width: 100%;
    top: $header-size;
    left: 0;
    background-color: rgba(255, 255, 255, 0.95);
    border-top: 1px solid $key-gray2;
    padding: 42px;
    > .inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
  .headProductNav {
    display: flex;
    @include vwpxpc(gap, 60);
    .item {
      @include vwpxpc(width, 126);
      text-align: center;
      span.space {
        display: none;
      }
    }
    a {
      .col-img {
        position: relative;
        span {
          display: block;
          &.over {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            // scale: 0;
            transform-origin: 50% 50%;
            transition: 0.4s;
          }
        }
        img {
          @include imgset;
        }
      }
      .col-content {
        transition: 0.3s;
      }
      .enTx {
        font-family: $font-sub;
        line-height: (13/24) + em;
        font-size: clamp_vw(22, 24);
      }
      .jaTx {
        font-size: 1rem;
        line-height: 1em;
        margin-top: 10px;
        font-weight: 200;
      }
    }
    a:hover {
      opacity: 0.9;
      .col-img {
        span.over {
          // scale: 1;
          opacity: 1;
        }
      }
      .col-content {
        transform: translateX(3px);
      }
    }
    a.current {
      cursor: default;
      pointer-events: none;
      .col-img {
        span.over {
          opacity: 1;
        }
      }
    }
  }
}
@media screen and (min-width: 820px) {
  .headProductNav {
    .item {
      br {
        display: none;
      }
      span.space {
        display: inline;
      }
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
  .headShopNav {
    width: $grid_03 + px;
    opacity: 0;
    transition: opacity 0.5s;
    &.active {
      opacity: 1;
    }
    li {
      font-size: 1rem;
      margin-bottom: 14px;
    }
    a {
      display: block;
      position: relative;
      &::before {
        @include iconFont;
        content: '\EA02';
        position: absolute;
        top: 0;
        right: 10px;
        transition: 0.3s;
        opacity: 0;
      }
      .labelTx {
        font-family: $font-ja;
        font-weight: 400;
        transition: 0.3s;
        display: inline-block;
      }
    }
    a:hover {
      .labelTx {
        opacity: 0.9;
        transform: translateX(2px);
      }
      &::before {
        right: 0;
        opacity: 1;
      }
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
}
