@charset "utf-8";
* {
  @include box-sizing(border-box);
}

*:before,
*:after {
  @include box-sizing(border-box);
}

html {
  //height: 100%;
  font-size: 62.5%;
  visibility: hidden;
  //overflow: hidden;
  //height: -webkit-fill-available;
}

html.wf-active,
html.loading-delay {
  visibility: visible;
}

body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
  color: $text-color;
  font-family: $base-font-family;
  line-height: $base-line-height;
  background-color: $bg-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-rendering: optimizelegibility;
  font-size: 1.4rem;
  font-weight: 400;
  text-size-adjust: none;
  -webkit-text-size-adjust: none;
  position: relative;
  @media screen and (max-width: 768px) {
    height: 100dvh;
  }
}

input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

::selection,
::-moz-selection {
  color: #fff;
  background-color: #eee;
}

p,
span {
  font-weight: 400;
}

iframe {
  border: none;
}

a {
  text-decoration: none;
}

.pc {
  display: none !important;
}

.sp {
  display: block !important;
}

.pcInline {
  display: none !important;
}

.spInline {
  display: inline !important;
}

.pcInlineB {
  display: none !important;
}

.spInlineB {
  display: inline-block !important;
}

.pcTd {
  display: none !important;
}

.spTd {
  display: table-cell !important;
}

.pcTr {
  display: none !important;
}

.spTr {
  display: table-row !important;
}

.pcTbl {
  display: none !important;
}

.spTbl {
  display: table !important;
}

.pcFlex {
  display: none !important;
}

.spFlex {
  display: flex !important;
}

a:hover,
a:active {
  //text-decoration:none!important;
  //color:inherit!important;
  img {
    @include opacity(1);
  }
}

//br.sp,br.spInline,br.spTd{
//	&:after {
//		margin-right: -.333em;
//		content:"";
//	}
//}

img {
  display: block;
  max-width: 100%;
}

.clearfix {
  *zoom: 1;
  &:before,
  &:after {
    content: ' ';
    display: table;
  }
  &:after {
    clear: both;
  }
}

.unqMrg {
  margin-top: -60px;
  padding-top: 60px;
}

html > body a.link_area {
  display: block;
  position: absolute;
  left: 0 !important;
  top: 0 !important;
  height: 100%;
  width: 100%;
  filter: alpha(opacity=0);
  -ms-filter: 'alpha( opacity=0 )';
  filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  z-index: 80;
  cursor: pointer;
}

@media screen and (min-width: $break-point-middle) {
  .pc {
    display: block !important;
  }

  .sp {
    display: none !important;
  }

  .pcInline {
    display: inline !important;
  }

  .spInline {
    display: none !important;
  }

  .pcInlineB {
    display: inline-block !important;
  }

  .spInlineB {
    display: none !important;
  }

  .pcTd {
    display: table-cell !important;
  }

  .spTd {
    display: none !important;
  }

  .pcTr {
    display: table-row !important;
  }

  .spTr {
    display: none !important;
  }

  .pcTbl {
    display: table !important;
  }

  .pcFlex {
    display: flex !important;
  }

  .spFlex {
    display: none !important;
  }

  .spTbl {
    display: none !important;
  }
}
