@charset "utf-8";
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
#outerMenu {
  position: relative;
  min-height: 100vh;
  .menuOpen &,
  .modalOpen & {
    overflow: hidden;
    position: fixed;
    width: 100%;
  }
}
@media screen and (min-width: $break-point-middle) {
  #outerMenu {
    padding-bottom: $footer-size;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
#menuPanel {
  background-color: rgba(255, 255, 255, 1);
  position: absolute;
  // position: fixed;
  top: -250%;
  left: 0;
  width: 100%;
  min-height: 100vh;
  z-index: 300;
  left: 0%;
  overscroll-behavior-y: contain;
  transition: top 0.6s $ease-out-quad;
  .menuOpen & {
    overflow: auto;
    z-index: 1999;
    top: 0;
  }
}
@media screen and (min-width: $break-point-middle) {
  #menuPanel {
    display: none;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.menuCloseArea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  bottom: 0;
  background-color: rgba(255, 255, 255, 1);
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.menuInner {
  min-height: 100vh;
  width: 100%;
  // height: 100%;
  position: relative;
  transition: transform 0.6s $ease-out-quad;
  // overflow: clip;
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.menuContent {
  // overflow: auto;
  padding-top: $header-size-sp;
  min-height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  opacity: 0;
  .menuOpen & {
    animation: menuFade 0.6s $ease-out-quad 0.6s forwards;
  }
  > .inner {
    width: 100%;
  }
}
// @media screen and (min-width: $break-point-middle) {
//   .menuContent {
//   }
// }
// @media screen and (min-width: $break-point-middle) and (min-height: 580px) {
//   .menuContent {
//     overflow: visible;
//     height: auto;
//   }
// }
@keyframes menuFade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
_::-webkit-full-page-media,
_:future,
:root .menuContent {
  height: 100svh;
}
@media screen and (min-width: $break-point-middle) {
  _::-webkit-full-page-media,
  _:future,
  :root .menuContent {
    height: 100vh;
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
#clickBlocker {
  width: 100%;
  height: 0%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  // background-color: $key-black;
  // background-color: rgba(255, 255, 255, 0.9);
  // background-color: $black;
  transition: 0.3s $ease-out-quad;
  //background-color:rgba(255, 255, 255, .8);
  //display: none;
  .menuOpen &,
  .searchOpen &,
  .modalOpen & {
    //display: block;
    // width: 100%;
    height: 100%;
    z-index: 600;
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.spMenuNav {
  .nav-item {
    border-bottom: 1px solid $key-gray2;
    > a,
    .head {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 110 / $design-num-sp * 100vw;
      line-height: (13/26) + em;
      @include fz_vw(26);
      font-family: $font-dec;
      > .labelTx {
        font-weight: 400;
      }
    }
    > a {
      > .labelTx {
        @include over_02;
        &::before {
          bottom: -1px;
        }
      }
      &.current {
        cursor: default;
        pointer-events: none;
        > .labelTx {
          &::before {
            display: none;
          }
        }
      }
    }
    .head {
      position: relative;
      cursor: pointer;
      &::before {
        @include iconFont;
        content: '\EA01';
        position: absolute;
        @include fz_vw(14);
        right: 50 / $design-num-sp * 100vw;
        top: calc(50% - 0.5em);
        transition: 0.3s;
      }
    }
    &.active {
      .head {
        &::before {
          transform: rotate(-180deg);
        }
      }
    }
  }
  .childCont {
    display: none;
    @include fz_vw(13);
    padding-bottom: 32 / $design-num-sp * 100vw;
    ul {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 24 / $design-num-sp * 100vw;
    }
    a {
      .labelTx {
        font-weight: 400;
        @include over_02;
      }
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.spProductNav {
  .item {
    margin-bottom: 16 / $design-num-sp * 100vw;
    &:last-child {
      margin-bottom: 50 / $design-num-sp * 100vw;
    }
    br {
      display: none;
    }
    a {
      position: relative;
      display: block;
      text-align: center;
      .col-img {
        position: absolute;
        right: 30 / $design-num-sp * 100vw;
        top: -7 / $design-num-sp * 100vw;
        .normal {
          display: block;
          width: 54 / $design-num-sp * 100vw;
        }
        .over {
          display: none;
        }
      }
      .enTx {
        font-family: $font-sub3;
        font-style: italic;
        line-height: (40/40) + em;
        @include fz_vw(40);
        @include over_02;
        display: inline-block;
        letter-spacing: -0.01em;
        &::before {
          bottom: 0.1em;
          opacity: 0;
        }
      }
      .jaTx {
        display: none;
      }
      &.current {
        cursor: default;
        pointer-events: none;
        .enTx {
          &::before {
            opacity: 1;
          }
        }
      }
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.menuFooter {
  padding-bottom: 100 / $design-num-sp * 100vw;
  padding-top: 70 / $design-num-sp * 100vw;
  text-align: center;
}
