@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.secStyle_01 {
  position: relative;
  > .inner {
    position: relative;
  }
  &.style_01 {
    border-top: 1px solid $key-gray2;
  }
}
///////////////////////////////////////
.secStyle_02 {
  position: relative;
  border-top: 1px solid $key-gray2;
  border-bottom: 1px solid $key-gray2;
  > .inner {
    position: relative;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.itemWrapStyle_01 {
  position: relative;
  border-top: 1px solid $key-gray2;
  > * {
    border-bottom: 1px solid $key-gray2;
  }
}
//////////////////////////////////////////////
.itemWrapStyle_02 {
  > * {
    margin-bottom: 44 / $design-num-sp * 100vw;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemWrapStyle_02 {
    > * {
      margin-bottom: 44px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
//////////////////////////////////////////////
.itemWrapStyle_03 {
  > * {
    margin-bottom: 36 / $design-num-sp * 100vw;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemWrapStyle_03 {
    > * {
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
//////////////////////////////////////////////
.itemWrapStyle_04 {
  display: flex;
  flex-direction: column;
  gap: 40 / $design-num-sp * 100vw;
}
@media screen and (min-width: $break-point-middle) {
  .itemWrapStyle_04 {
    gap: 40px;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.elemWrapStyle_01 {
  display: flex;
  justify-content: center;
  > * {
    width: 184 / $design-num-sp * 100vw;
  }
}
@media screen and (min-width: $break-point-middle) {
  .elemWrapStyle_01 {
    display: flex;
    justify-content: center;
    > * {
      width: 164px;
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.colStyle_01 {
  .col-ttl {
    margin-bottom: 28 / $design-num-sp * 100vw;
    text-align: center;
  }
}
@media screen and (min-width: $break-point-middle) {
  .colStyle_01 {
    @include setTbl;
    > * {
      vertical-align: top;
    }
    .col-ttl {
      width: 204 / $w_02 * 100%;
      padding-right: 2em;
      margin-bottom: 0;
      text-align: left;
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
}
