a,
a:hover {
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

a img:hover {
  opacity: 0.7;
  filter: alpha(opacity=70);
  /* ie lt 8 */
  -ms-filter: 'alpha(opacity=70)';
  /* ie 8 */
  -moz-opacity: 0.7;
  /* FF lt 1.5, Netscape */
  -khtml-opacity: 0.7;
  /* Safari 1.x */
}

/*Firefoxだけに適用されるCSSハック*/
@-moz-document url-prefix() {
  a img:hover {
    opacity: 0.8;
  }
}

/*IE10以降に適用されるCSSハック（一応）*/
@media all and (-ms-high-contrast: none) {
  a img:hover {
    opacity: 0.7;
    filter: alpha(opacity=70);
    /* ie lt 8 */
    -ms-filter: 'alpha(opacity=70)';
    /* ie 8 */
    -moz-opacity: 0.7;
    /* FF lt 1.5, Netscape */
    -khtml-opacity: 0.7;
    /* Safari 1.x */
  }
}

/*IE11だけに適用されるCSSハック*/
@media all and (-ms-high-contrast: none) {
  *::-ms-backdrop,
  a img:hover {
    opacity: 0.7;
    filter: alpha(opacity=70);
    /* ie lt 8 */
    -ms-filter: 'alpha(opacity=70)';
    /* ie 8 */
    -moz-opacity: 0.7;
    /* FF lt 1.5, Netscape */
    -khtml-opacity: 0.7;
    /* Safari 1.x */
  }
}

