@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@keyframes baseFade_01 {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
////////////////////////////////////
@keyframes baseFade_02 {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
////////////////////////////////////
@keyframes rotate_01 {
  0% {
    rotate: 0deg;
  }
  to {
    rotate: 3deg;
  }
}
