@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
footer {
  position: relative;
  border-top: 1px solid $key-gray2;
  > .wrap {
    // padding-top: 213 / $design-num-sp * 100vw;
    padding-top: 150 / $design-num-sp * 100vw;
    padding-bottom: 30 / $design-num-sp * 100vw;
    > .inner {
      display: flex;
      flex-direction: column-reverse;
      gap: 40 / $design-num-sp * 100vw;
    }
  }
  .col-logo {
    img {
      @include imgset;
    }
    a {
      display: block;
      &.current {
        cursor: default;
        pointer-events: none;
      }
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    > .wrap {
      height: $footer-size;
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 120px;
      padding-bottom: 50px;
      > .inner {
        display: block;
      }
    }
    .col-logo {
      position: relative;
      transform: translateX(-44px);
      a:hover {
        opacity: 0.9;
        transform: translateX(3px);
      }
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.footNav {
  margin-top: 24 / $design-num-sp * 100vw;
  span,
  a {
    font-weight: 400;
    display: inline-block;
    &.current {
      cursor: default;
      pointer-events: none;
      .labelTx {
        text-decoration: underline;
      }
    }
  }
  .inner {
    display: flex;
    flex-direction: column;
  }
  .inner > ul {
    @include fz_vw(14);
    margin-bottom: (18/14) + em;
    &:last-child {
      margin-bottom: 0;
    }
    &.parent {
      @include fz_vw(14);
      > li {
        margin-bottom: (18/14) + em;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  li.hasChild {
    > a,
    > span {
      display: flex;
      align-items: center;
      gap: 5 / $design-num-sp * 100vw;
      &::after {
        @include iconFont;
        content: '\EA01';
        font-weight: 400;
        @include fz_vw(10);
        transition: 0.3s;
      }
    }
    &.active {
      > a,
      > span {
        &::after {
          transform: rotate(-180deg);
        }
      }
    }
    ul.child {
      display: none;
    }
  }
  ul.child {
    @include fz_vw(12);
    line-height: (26/12) + em;
  }
  ul.child.style_01,
  .inner > ul.normal {
    @include fz_vw(10);
    line-height: (26/10) + em;
  }
}
@media screen and (min-width: $break-point-middle) {
  .footNav {
    margin-top: 24px;
    a {
      @include over_01($key-color1);
      &::before {
        bottom: 0.4em;
      }
      &.current {
        .labelTx {
          text-decoration: none;
        }
        &::before {
          width: 100%;
        }
      }
    }
    a:hover {
      color: $key-color1;
      transform: translateX(2px);
    }
    .inner {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    .inner > ul {
      width: 164 / $w_02 * 100%;
      font-size: 1.4rem;
      margin-bottom: 0;
      &.parent {
        font-size: 1.4rem;
        > li {
          margin-bottom: 1em;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
    li.hasChild {
      > a,
      > span {
        display: inline;
        &::after {
          display: none;
        }
      }
      ul.child {
        display: block;
      }
    }
    ul.child,
    ul.child.style_01,
    .inner > ul.normal {
      // font-size: 1.2rem;
      font-size: 1rem;
      line-height: (26/12) + em;
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
li.copyRight {
  margin-top: 44 / $design-num-sp * 100vw;
}
@media screen and (min-width: $break-point-middle) {
  li.copyRight {
    margin-top: (130/16) + em;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
small {
  @include fz_vw(16);
  font-family: $font-sub3;
  font-style: italic;
  // text-decoration: underline;
  font-weight: 400;
}
@media screen and (min-width: $break-point-middle) {
  small {
    font-size: 1.6rem;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.breadCrumbSec {
  position: absolute;
  top: 0;
  left: 0;
  @include fz_vw(12);
  width: 100%;
  padding-left: $sp-pad;
  padding-right: $sp-pad;
  > .inner {
    display: flex;
    height: 38 / $design-num-sp * 100vw;
    align-items: center;
  }
  .home & {
    display: none;
  }
  ol {
    display: flex;
    align-items: center;
    gap: 12 / $design-num-sp * 100vw;
  }
  li {
    display: flex;
    align-items: center;
    gap: 12 / $design-num-sp * 100vw;
    &::after {
      @include iconFont;
      content: '\EA02';
      @include fz_vw(10);
    }
    &:last-child::after {
      display: none;
    }
  }
  a {
    display: flex;
    &.current {
      cursor: default;
      pointer-events: none;
    }
  }
  span {
    font-weight: 400;
  }
}
@media screen and (min-width: $break-point-middle) {
  .breadCrumbSec {
    font-size: 1rem;
    padding-left: $base-pad;
    padding-right: $base-pad;
    > .inner {
      width: 100%;
      max-width: $w_01 + px;
      margin-left: auto;
      margin-right: auto;
      height: 60px;
    }
    ol {
      // gap: (40/10) + em;
      gap: 0;
    }
    li {
      width: $grid_01/2 + px;
      gap: 0;
      position: relative;
      // gap: (40/10) + em;
      &::after {
        font-size: 1rem;
        position: absolute;
        right: 3em;
        top: 0;
      }
    }
    a:hover {
      transform: translateX(2px);
      color: $key-color1;
    }
  }
}
