.setSp .sp-way {
  opacity: 0;
  transform: translateY(10px);
  transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
  &.is-active-sp {
    opacity: 1;
    transform: translateY(0);
  }
}
.way {
  @include opacity(0);
  @include transform(translateY(-10px));
  @include transition(all 1s cubic-bezier(0.645, 0.045, 0.355, 1));
  &.is-active,
  .showFlag & {
    .loaded & {
      @include opacity(1);
      @include transform(translateY(0px));
    }
  }
}
.autoWay {
  @include opacity(0);
  @include transform(translateY(10px));
  @include transition(all 1s cubic-bezier(0.645, 0.045, 0.355, 1));
  .loaded & {
    @include opacity(1);
    @include transform(translateY(0px));
  }
}
@media screen and (min-width: $break-point-middle) {
  .autoWay {
    @include opacity(0);
    @include transform(translateY(10px));
    @include transition(all 1s cubic-bezier(0.645, 0.045, 0.355, 1));
    .loaded & {
      @include opacity(0);
      @include transform(translateY(30px));
    }
    &.is-active {
      @include opacity(1);
      @include transform(translateY(0px));
    }
  }
}
.fastWay {
  @include opacity(0);
  @include transform(translateY(30px));
  @include transition(all 1s cubic-bezier(0.645, 0.045, 0.355, 1));
  &.active,
  .showContFlag & {
    @include opacity(1);
    @include transform(translateY(0px));
  }
}
.alphaWay {
  @include opacity(0);
  @include transition(all 0.6s cubic-bezier(0.645, 0.045, 0.355, 1) 0s);
  &.is-active {
    @include opacity(1);
  }
}
.scaleWay {
  opacity: 0;
  transition: 0.4s $ease-in-out-circ;
  scale: 0;
  transform-origin: 50% 50%;
  &.is-active {
    opacity: 1;
    scale: 1;
  }
}
////////////////////////////////////////////////
////////////////////////////////////////////////
.imgEffect_01 {
  > span {
    display: block;
    transition: 1s $ease-out-quad;
    overflow: hidden;
    position: relative;
    left: -100%;
    opacity: 0;
    img {
      position: relative;
      left: 100%;
      transition: 1s $ease-out-quad;
    }
  }
  &.is-active {
    > span {
      left: 0;
      opacity: 1;
      img {
        left: 0;
      }
    }
  }
}
/////////////////////////////////
.imgEffect_02 {
  @extend .imgEffect_01;
  > span {
    left: auto;
    right: -100%;
    img {
      left: auto;
      right: 100%;
    }
  }
  &.is-active {
    > span {
      left: auto;
      right: 0;
      img {
        left: auto;
        right: 0;
      }
    }
  }
}
////////////////////////////////////////
.imgEffect_03 {
  display: block;
  opacity: 0;
  transform: translateY(30);
  transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1) 0.5s;
  &.is-active {
    opacity: 1;
    transform: translateY(0);
  }
}
////////////////////////////////////////
.imgEffect_04 {
  overflow: hidden;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: $white;
    .is-active & {
      animation: 0.6s slideOutRight $ease-out-quad 0.1s forwards;
    }
  }
}
////////////////////////////////////////
.imgEffect_05 {
  display: block;
  opacity: 0;
  transform: translateY(30);
  transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1) 0.5s;
  .is-active & {
    opacity: 1;
    transform: translateY(0);
  }
}
////////////////////////////////////////
.imgEffect_06 {
  display: block;
  opacity: 0;
  transform: translateY(30);
  transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1) 0.5s;
  &.is-active {
    opacity: 1;
    transform: translateY(0);
  }
}
@media screen and (min-width: $break-point-middle) {
  .imgEffect_06 {
    opacity: 1;
    transform: translateY(0);
  }
}
