.txEffect_02 {
  @include txEffectFormat_02;
  .is-active & {
    opacity: 1;
    transform: translateY(0);
  }
}
///////////////////////////////
.txEffect_03 {
  @include txEffectFormat_01;
  &.is-active-item {
    > span > span {
      @for $i from 1 through 40 {
        &:nth-child(#{$i}) {
          animation: txEffect_01 0.8s $ease-in-out-sine 0s + 0.02 * $i forwards;
        }
      }
    }
  }
}
///////////////////////////////
.txEffect_04 {
  @include txEffectFormat_02;
  .is-active-item & {
    opacity: 1;
    transform: translateY(0);
  }
}
///////////////////////////////
.txEffect_05 {
  svg {
    display: block;
    overflow: visible;
  }
  path {
    position: relative;
    display: inline-block;
    opacity: 0;
    transform: translateY(5px);
    // .loaded & {
    //   opacity: 1;
    // }
    @for $i from 1 through 20 {
      &:nth-child(#{$i}) {
        .is-active & {
          animation: txEffect_00 0.8s $ease-in-out-sine 0s + 0.1 * $i forwards;
        }
      }
    }
  }
}
////////////////////////////////////////////////
@keyframes txEffect_00 {
  0% {
    opacity: 0;
    transform: translateY(5px);
  }
  // 80% {
  //   top: -0.3em;
  // }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
////////////////////////////////////////////////
$timing_06: 0.4s;
$speed_06: 0.03;
.txEffect_06 {
  @include txEffectFormat_01;
  // overflow: hidden;
  > span {
    display: block;
  }
  > span:nth-child(1) > span {
    @for $i from 1 through 100 {
      &:nth-child(#{$i}) {
        .is-active & {
          animation: txEffect_01 $timing_06 $ease-in-out-sine 0s + $speed_06 * $i forwards;
        }
      }
    }
  }
  > span:nth-child(2) > span {
    @for $i from 1 through 100 {
      &:nth-child(#{$i}) {
        .is-active & {
          animation: txEffect_01 $timing_06 $ease-in-out-sine 0.6s + $speed_06 * $i forwards;
        }
      }
    }
  }
  > span:nth-child(3) > span {
    @for $i from 1 through 40 {
      &:nth-child(#{$i}) {
        .is-active & {
          animation: txEffect_01 $timing_06 $ease-in-out-sine 1.6s + $speed_06 * $i forwards;
        }
      }
    }
  }
  // &.style_01 {
  //   > span > span:nth-child(10) {
  //     display: block;
  //     height: 0;
  //   }
  // }
}
