.txEffect_07 {
  @include txEffectFormat_01;
  > span {
    display: block;
  }
  > span > span {
    @for $i from 1 through 40 {
      &:nth-child(#{$i}) {
        .swiper-slide-active & {
          animation: txEffect_01 0.8s $ease-in-out-sine 0s + 0.04 * $i forwards;
        }
      }
    }
  }
}
////////////////////////////////////////////////
.txEffect_08 {
  @include txEffectFormat_01;
  > span {
    display: block;
