@keyframes linemove_01 {
  0% {
    @include transform(translateX(0));
  }
  50% {
    @include transform(translateX(100%));
  }
  50.01% {
    @include transform(translateX(-100%));
  }
  to {
    @include transform(translateX(0));
  }
}
////////////////////////////////////////////////
@keyframes linemove_02 {
  0% {
    @include scale(100%, 100%);
  }
  50% {
    @include scale(0%, 100%);
  }
  to {
    @include scale(100%, 100%);
  }
}
