@charset "utf-8";
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.send_error,
p.error {
  color: $err-color;
}
.formStyle {
  font-family: $font-ja;
  .errTx {
    color: $attention-color;
    @include fz_vw(12);
    font-weight: 400;
    padding-top: 0.5em;
  }
}
@media screen and (min-width: $break-point-middle) {
  .formStyle {
    .errTx {
      font-size: 1.2rem;
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.formSec {
  .ttl {
    display: flex;
    gap: 3px;
    margin-bottom: 12 / $design-num-sp * 100vw;
    p {
      @include fz_vw(14);
      font-weight: 400;
      position: relative;
    }
    &::after {
      font-weight: 500;
      letter-spacing: 0.1em;
      content: '任意';
      display: flex;
      min-width: 30 / $design-num-sp * 100vw;
      height: 16 / $design-num-sp * 100vw;
      border-radius: 100px;
      @include fz_vw(8);
      line-height: 1em;
      justify-content: center;
      align-items: center;
      padding-left: 0.5em;
      padding-right: 0.5em;
      border: 1px solid $key-gray1;
      .secConfirm & {
        display: none;
      }
    }
  }
  &.req {
    .ttl {
      &::after {
        font-weight: 600;
        content: '必須';
        color: $white;
        background-color: $black;
        border: 1px solid $black;
      }
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .formSec {
    .ttl {
      margin-bottom: 10px;
      p {
        font-size: 1.4rem;
      }
      &::after {
        font-weight: 600;
        min-width: 30px;
        height: 16px;
        font-size: 0.8rem;
      }
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.formCol {
  padding-left: $sp-pad;
  padding-right: $sp-pad;
  &.style_02 {
    display: flex;
    flex-direction: column;
    gap: 14 / $design-num-sp * 100vw;
  }
}
@media screen and (min-width: $break-point-middle) {
  .formCol {
    padding-left: 0;
    padding-right: 0;
    &.style_02 {
      flex-direction: row;
      justify-content: space-between;
      gap: 0;
      .col {
        width: 370 / $w_02 * 100%;
      }
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.formBtnWrap {
  display: flex;
  gap: 20 / $design-num-sp * 100vw;
  flex-direction: column-reverse;
  button {
    width: 100%;
  }
}
@media screen and (min-width: $break-point-middle) {
  .formBtnWrap {
    display: flex;
    flex-direction: row;
    gap: 20px;
    > * {
      width: 370 / $w_02 * 100%;
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.radioStyle_01 {
  li {
    margin-bottom: 5 / $design-num-sp * 100vw;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
  }
  input {
    display: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  label {
    position: relative;
    padding-left: 34 / $design-num-sp * 100vw;
    cursor: pointer;
    &::before {
      content: '';
      width: 23 / $design-num-sp * 100vw;
      height: 23 / $design-num-sp * 100vw;
      border-radius: 100%;
      border: 1px solid $key-gray1;
      position: absolute;
      top: 0;
      left: 0px;
    }
  }
  input + span {
    display: block;
    width: 11 / $design-num-sp * 100vw;
    height: 11 / $design-num-sp * 100vw;
    background-color: $black;
    border-radius: 100%;
    position: absolute;
    top: 6 / $design-num-sp * 100vw;
    left: 6 / $design-num-sp * 100vw;
    opacity: 0;
    transition: 0.3s;
  }
  input:checked + span {
    opacity: 1;
  }
  .labelTx {
    font-weight: 400;
    @include fz_vw(14);
    transition: 0.3s;
    position: relative;
    top: -0.1em;
  }
}
@media screen and (min-width: $break-point-middle) {
  .radioStyle_01 {
    li {
      margin-bottom: 5px;
      left: 20px;
      &:last-child {
        margin-bottom: 0;
      }
      label {
        padding-left: 41px;
      }
    }
    label {
      padding-left: 32px;
      &::before {
        width: 19px;
        height: 19px;
      }
      &:hover {
        opacity: 0.9;
        .labelTx {
          transform: translateX(2px);
        }
      }
    }
    input + span {
      width: 9px;
      height: 9px;
      top: 5px;
      left: 5px;
    }
    .labelTx {
      font-size: 1.4rem;
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.inputStyle_01 {
  position: relative;
  input,
  textarea {
    position: relative;
    font-weight: 400;
    @include appearance(none);
    margin: 0;
    border-radius: 26px;
    border: 1px solid $key-gray1;
    outline: none;
    @include fz_vw(16);
    z-index: 2;
    line-height: 1.5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    box-sizing: border-box;
    width: 100%;
    background-color: transparent;
    transition: 0.2s;
    color: $black;
    letter-spacing: 0.03em;
    height: 44 / $design-num-sp * 100vw;
    &:read-only {
      cursor: default;
      pointer-events: none;
    }
    &[type='number']::-webkit-outer-spin-button,
    &[type='number']::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
    &[type='number'] {
      -moz-appearance: textfield;
    }
    /* 各ベンダープレフィックスが取れた標準版！！(http://caniuse.com/#search=placeholder-shown) */
    &:placeholder-shown {
      color: $key-gray1;
    }
    /* Google Chrome, Safari, Opera 15+, Android, iOS */
    &::-webkit-input-placeholder {
      color: $key-gray1;
    }
    /* Firefox 18- */
    &:-moz-placeholder {
      color: $key-gray1;
    }
    /* Firefox 19+ */
    &::-moz-placeholder {
      color: $key-gray1;
    }
    /* IE 10+ */
    &:-ms-input-placeholder {
      color: $key-gray1;
    }
    &:-webkit-autofill {
      box-shadow: 0 0 0 1000px $white inset;
    }
  }
  textarea {
    border: 1px solid $key-gray1;
    resize: none;
    height: auto;
    line-height: (30/22) + em;
    padding: 1.5em;
    &:focus {
      border: 1px solid $black;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .inputStyle_01 {
    input,
    textarea {
      // font-size: clamp_vw(16, 22);
      font-size: 1.2rem;
      // padding-left: 16px;
      // padding-right: 16px;
      height: auto;
      min-height: 44px;
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.hiddenBtns {
  text-align: center;
  input,
  button {
    display: none;
  }
  .wpcf7-spinner {
    margin: 20px 0 10px;
  }
}
.hiddenInput {
  input {
    display: none;
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.selectStyle_01 {
  position: relative;
  select {
    color: $key-color1;
    width: 100%;
    position: relative;
    z-index: 2;
    border: none;
    @include appearance(none);
    @include fz_vw(16);
    //line-height:(38/16)+em;
    background-color: transparent;
    padding-right: 20 / $design-num-sp * 100vw;
    outline: 0;
    @include box-shadow(0);
    font-weight: 400;
    border-bottom: 1px solid $key-gray1;
    height: 32 / $design-num-sp * 100vw;
    line-height: 1.2em;
    &:focus {
      @include box-shadow(0);
      outline: 0;
      border-bottom: 1px solid $key-gray2;
    }
  }
  select::-ms-expand {
    display: none;
  }
}
@media screen and (min-width: $break-point-middle) {
  .selectStyle_01 {
    &:before {
      right: 24px;
    }
    select {
      font-size: 1.4rem;
      padding-right: 2em;
      height: auto;
      min-height: 36px;
    }
    option {
      width: 100%;
    }
    &:hover {
      .inputIcon {
        transform: translateY(2px);
      }
    }
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.confirmTx {
  font-weight: 400;
  @include fz_vw(14);
  line-height: (30/22) + em;
}
@media screen and (min-width: $break-point-middle) {
  .confirmTx {
    font-size: 1.4rem;
  }
}
