@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.itemStyle_01 {
  text-align: center;
  a {
    .eyecatch {
      .imgStyle {
        position: relative;
        left: -20 / $design-num-sp * 100vw;
      }
      img {
        @include imgset;
      }
    }
    .caption {
      color: $key-color1;
      font-weight: 400;
      @include fz_vw(10);
      position: relative;
      margin-top: -1.5em;
      letter-spacing: 0.06em;
    }
    .head {
      margin-bottom: 18 / $design-num-sp * 100vw;
    }
    .content {
      transition: 0.3s;
    }
    .ttlEn {
      font-family: $font-sub;
      line-height: (14/30) + em;
      @include fz_vw(30);
      margin-top: 5 / $design-num-sp * 100vw;
      letter-spacing: -0.05em;
    }
    .ttlJa {
      font-weight: 200;
      @include fz_vw(10);
      margin-top: 0.5em;
    }
    .btnCol {
      display: flex;
      justify-content: center;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemStyle_01 {
    a {
      .eyecatch {
        .imgStyle {
          @include vwpxpc(left, -20);
        }
      }
      .caption {
        font-size: 1rem;
      }
      .head {
        margin-bottom: 16px;
      }
      .ttlEn {
        font-size: 3rem;
        margin-top: 5px;
      }
      .ttlJa {
        font-size: 1rem;
      }
    }
    a:hover {
      opacity: 0.9;
      .content {
        transform: translateX(2px);
      }
      .btn {
        .labelTx {
          transform: translateX(2px);
        }
        &::before {
          animation: linemove_02 0.8s cubic-bezier(0.58, 0.3, 0.005, 1) 0s 1;
        }
      }
    }
  }
}
//////////////////////////////////////////
.itemStyle_02 {
  padding-top: 54 / $design-num-sp * 100vw;
  padding-bottom: 48 / $design-num-sp * 100vw;
  a {
    display: block;
    position: relative;
    .eyecatch {
      position: relative;
      margin-bottom: 115 / $design-num-sp * 100vw;
    }
    .caption {
      line-height: 1em;
      @include fz_vw(14);
      text-align: center;
      font-weight: 400;
      margin-bottom: 34 / $design-num-sp * 100vw;
      letter-spacing: 0.03em;
    }
    .imgStyle {
      overflow: hidden;
      img {
        @include imgset;
        transition: 0.8s;
      }
    }
    .head {
      position: absolute;
      top: 332 / $design-num-sp * 100vw;
      left: 0;
      width: 100%;
      transition: 0.3s;
      text-align: center;
      > .inner {
        position: relative;
        @include fz_vw(80);
      }
    }
    .ttlEn {
      // line-height: (46/80) + em;
      line-height: (50/80) + em;
      @include fz_vw(80);
      font-family: $font-dec;
      @media screen and (max-width: $break-point-middle-sp) {
        &.style_01 {
          text-shadow: 0px -5px 10px rgba(255, 255, 255, 0.4);
        }
      }
    }
    .ttlJa {
      font-weight: 400;
      @include fz_vw(14);
      letter-spacing: 0.03em;
    }
    .content {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      gap: 16 / $design-num-sp * 100vw;
    }
    .col-img {
      ul {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 12 / $design-num-sp * 100vw;
      }
      li {
        width: 70 / $design-num-sp * 100vw;
      }
      img {
        @include imgset;
        // width: 100%;
        // display: block;
        @include lensShadow;
      }
    }
    .col-spec {
      display: flex;
      justify-content: center;
      text-align: center;
      flex-wrap: wrap;
      ul {
        display: flex;
      }
      li {
        font-weight: 400;
        line-height: (16/12) + em;
        @include fz_vw(12);
        &::after {
          content: '|';
          padding: 0 0.2em;
        }
        &.style_01 {
          &::after {
            opacity: 0;
          }
        }
      }
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemStyle_02 {
    @include vwpxpc(padding-top, 84);
    @include vwpxpc(padding-bottom, 54);
    a {
      .eyecatch {
        @include vwpxpc2(height, 530, $w_02 + $base-pad-num * 2);
        margin-bottom: 0;
      }
      .caption {
        font-size: 1.4rem;
        margin-bottom: 34px;
      }
      .head {
        top: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        > .inner {
          font-size: 11rem;
        }
      }
      .ttlEn {
        line-height: (70/110) + em;
        font-size: 11rem;
      }
      .ttlJa {
        font-size: 1.2rem;
      }
      .content {
        flex-direction: row-reverse;
        margin-top: -24px;
        position: relative;
        gap: 0;
      }
      .col-img {
        width: 246 / $w_02 * 100%;
        ul {
          gap: 10/246 * 100%;
        }
        li {
          width: 105/246 * 100%;
        }
      }
      .col-spec {
        flex-grow: 1;
        justify-content: space-between;
        flex-wrap: nowrap;
        @include vwpxpc2(gap, 54, $w_02 + $base-pad-num * 2);
        @include vwpxpc2(padding-left, 16, $w_02 + $base-pad-num * 2);
        @include vwpxpc2(padding-right, 16, $w_02 + $base-pad-num * 2);
        ul {
          display: block;
        }
        li {
          font-size: 1.2rem;
          &::after {
            display: none;
          }
        }
      }
    }
    a:hover {
      opacity: 0.95;
      .imgStyle {
        img {
          scale: 1.03;
        }
      }
      .head {
        transform: translateX(5px);
      }
    }
    &.style_02 {
      a {
        .head {
          justify-content: flex-start;
        }
      }
    }
  }
}
//////////////////////////////////////////
.itemStyle_03 {
  .col-img {
    margin-bottom: 18 / $design-num-sp * 100vw;
    .imgStyle {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: auto;
      margin-right: auto;
    }
    img {
      display: block;
      width: 100%;
      // @include imgset;
    }
  }
  .col-content {
    padding-left: 24 / $w_05 * 100%;
    padding-right: 42 / $w_05 * 100%;
    text-align: center;
  }
  .ttl {
    font-weight: 400;
    @include fz_vw(14);
    margin-bottom: 10 / $design-num-sp * 100vw;
  }
  .summary {
    line-height: (18/12) + em;
    @include fz_vw(12);
    sup {
      @include fz_vw(8);
      font-weight: 400;
      position: relative;
      top: -0.2em;
    }
    p {
      font-weight: 400;
      &.style_01 {
        @include fz_vw(10);
      }
    }
  }
  &.style_01 {
    .imgStyle {
      width: 127 / $design-num-sp * 100vw;
    }
  }
  &.style_02 {
    .imgStyle {
      width: 72 / $design-num-sp * 100vw;
      $gap: -5 / $design-num-sp * 100vw;
      transform: translateX($gap);
    }
  }
  &.style_03 {
    .imgStyle {
      width: 89 / $design-num-sp * 100vw;
    }
  }
  &.style_04 {
    .imgStyle {
      width: 78 / $design-num-sp * 100vw;
    }
  }
  &.style_05 {
    .imgStyle {
      width: 103 / $design-num-sp * 100vw;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemStyle_03 {
    > .inner {
      @include setTbl;
    }
    .col-img {
      width: 127 / $w_05 * 100%;
      .imgStyle {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
      }
      img {
        @include imgset;
      }
    }
    .col-content {
      padding-left: 24 / $w_05 * 100%;
      padding-right: 42 / $w_05 * 100%;
      text-align: left;
    }
    .ttl {
      font-weight: 400;
      font-size: 1.4rem;
      margin-bottom: 10px;
    }
    .summary {
      line-height: (18/12) + em;
      font-size: 1.2rem;
      sup {
        font-size: 0.8rem;
        font-weight: 400;
        position: relative;
        top: -0.2em;
      }
      p {
        font-weight: 400;
        &.style_01 {
          font-size: 1rem;
        }
      }
    }
    &.style_01 {
      .imgStyle {
        width: 127/127 * 100%;
      }
    }
    &.style_02 {
      .imgStyle {
        width: 72/127 * 100%;
        transform: translateX(0);
      }
    }
    &.style_03 {
      .imgStyle {
        width: 89/127 * 100%;
        transform: translateX(10/127 * 100%);
      }
    }
    &.style_04 {
      .imgStyle {
        width: 78/127 * 100%;
      }
    }
    &.style_05 {
      .imgStyle {
        width: 103/127 * 100%;
        transform: translateX(-10/127 * 100%);
      }
    }
  }
}
//////////////////////////////////////////
.itemStyle_04 {
  border-top: 1px solid $key-gray2;
  padding-top: 52 / $design-num-sp * 100vw;
  padding-bottom: 50 / $design-num-sp * 100vw;
  &:first-child {
    padding-top: 60 / $design-num-sp * 100vw;
  }
  img {
    @include imgset;
  }
  .genre {
    @include fz_vw(14);
    font-weight: 400;
  }
  > .genre {
    margin-bottom: 40 / $design-num-sp * 100vw;
    text-align: center;
  }
  > .wrap {
    padding-left: $sp-pad;
    padding-right: $sp-pad;
  }
  .sec-img {
    position: relative;
    .over {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transition: 0.6s;
      animation: 10s ease-in-out infinite fadeImg;
    }
  }
  .sec-ttl {
    text-align: center;
    .genre {
      display: none;
    }
    .ttlEn {
      line-height: (32/50) + em;
      font-family: $font-dec;
      @include fz_vw(50);
      font-weight: 400;
      margin-top: -0.25em;
    }
    .ttlJa {
      @include fz_vw(12);
      font-weight: 400;
    }
  }
  .sec-product {
    a {
      .product-img {
        width: 100 / $design-num-sp * 100vw;
        margin-left: auto;
        margin-right: auto;
        img {
          @include imgset;
          @media screen and (max-width: $break-point-middle-sp) {
            @include lensShadow;
          }
        }
      }
    }
  }
  .btnCol {
    margin-top: 12 / $design-num-sp * 100vw;
    display: flex;
    justify-content: center;
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemStyle_04 {
    padding-top: 16 / $design-num * 100vw;
    padding-bottom: 16 / $design-num * 100vw;
    // padding-top: 16px;
    // padding-bottom: 16px;
    &:first-child {
      padding-top: 16 / $design-num * 100vw;
      // padding-top: 16px;
    }
    .genre {
      font-size: 1.4rem;
    }
    > .genre {
      display: none;
    }
    > .wrap {
      width: 100%;
      // max-width: $design-width;
      // margin-left: auto;
      // margin-right: auto;
      padding-left: $base-pad;
      padding-right: $base-pad;
    }
    > .wrap > .inner {
      display: flex;
      width: 100%;
      align-items: stretch;
    }
    .col {
      border-right: 1px solid $key-gray2;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
    .grid {
      display: flex;
      &.style_01 {
        width: 2/3 * 100%;
        .col {
          width: 1/2 * 100%;
        }
      }
      &.style_02 {
        width: 1/3 * 100%;
      }
    }
    .sec-img {
      width: 340 / $design-num * 100vw;
      // @include vwpxpc(width, 340);
      // width: 340/398 * 100%;
      .imgStyle {
        padding-top: 20 / $design-num * 100vw;
        padding-bottom: 20 / $design-num * 100vw;
      }
      .over {
        animation: none;
      }
    }
    .sec-ttl {
      .genre {
        display: block;
        @include vwpxpc(margin-bottom, 24);
      }
      .ttlEn {
        line-height: (50/80) + em;
        // @include fz_vwpcmax(80);
        @include fz_vwpc(80);
        margin-top: 0;
      }
      .ttlJa {
        font-size: 1.2rem;
      }
    }
    .sec-product {
      a {
        .product-img {
          width: 183 / $design-num * 100vw;
          // @include vwpxpc(width, 183);
          margin-left: 0;
          margin-right: 0;
          position: relative;
          $gap: -16 / $design-num * 100vw;
          transform: translateX(#{$gap});
        }
      }
      a:hover {
        opacity: 0.9;
        .btn {
          .labelTx {
            transform: translateX(2px);
          }
          &::before {
            animation: linemove_02 0.8s cubic-bezier(0.58, 0.3, 0.005, 1) 0s 1;
          }
        }
      }
    }
    .btnCol {
      margin-top: 0;
    }
    &.is-over {
      .sec-img {
        .over {
          opacity: 1;
        }
      }
    }
    &.style_01 {
      > .wrap > .inner {
        flex-direction: row-reverse;
      }
      .grid {
        &.style_01 {
          .col:last-child {
            border: none;
          }
        }
      }
      .sec-ttl {
        text-align: right;
      }
    }
    &.style_02 {
      .grid {
        flex-direction: row-reverse;
        &.style_01 {
          .col:first-child {
            border: none;
          }
        }
      }
      .sec-ttl {
        text-align: right;
      }
    }
    &.style_03 {
      > .wrap > .inner {
        flex-direction: row-reverse;
      }
      .grid {
        &.style_02 {
          .col:first-child {
            border: none;
          }
        }
      }
      .sec-ttl {
        text-align: left;
      }
    }
    &.style_04 {
      .grid {
        flex-direction: row-reverse;
        &.style_02 {
          .col:first-child {
            border: none;
          }
        }
      }
      .sec-ttl {
        text-align: left;
      }
    }
  }
}
////////////////////////////////////////////////
@keyframes fadeImg {
  0%,
  39% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  89% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
//////////////////////////////////////////
.itemStyle_05 {
  a {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    .col-content {
      white-space: nowrap;
      text-align: center;
    }
    .col-img {
      position: relative;
      margin-bottom: 38 / $design-num-sp * 100vw;
    }
    .catch {
      @include fz_vw(14);
      font-weight: 400;
      opacity: 0;
      transform: translateY(-5px);
      .swiper-slide-active & {
        animation: productSlideFade 0.6s $ease-out-quad forwards;
      }
    }
    .ttlEn {
      line-height: (32/50) + em;
      @include fz_vw(50);
      font-weight: 400;
      font-family: $font-dec;
    }
    .ttlJa {
      @include fz_vw(12);
      font-weight: 400;
      opacity: 0;
      transform: translateY(-5px);
      .swiper-slide-active & {
        animation: productSlideFade 0.6s $ease-out-quad 0.6s forwards;
      }
    }
    .productImg {
      position: absolute;
      bottom: -36 / $design-num-sp * 100vw;
      left: 0px;
      width: 100%;
      img {
        @include imgset;
        width: 100 / $design-num-sp * 100vw;
        margin-left: auto;
        margin-right: auto;
        @include lensShadow;
      }
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemStyle_05 {
    a {
      flex-direction: row;
      .col-content {
        text-align: left;
        width: 0;
        overflow: hidden;
        white-space: nowrap;
        transition: width 0.6s $ease-out-quad;
        .swiper-slide-active & {
          width: (795 - 486)/795 * 100%;
        }
      }
      .col-img {
        width: 486/795 * 100%;
        margin-bottom: 0;
        background-color: $white;
        .bgImg {
          img {
            opacity: 0.5;
            .swiper-slide-active & {
              opacity: 1;
            }
          }
        }
      }
      .catch {
        font-size: 1.4rem;
      }
      .ttlEn {
        line-height: (50/80) + em;
        @include fz_vwpcmax(80);
      }
      .ttlJa {
        font-size: 1.2rem;
      }
      .productImg {
        bottom: -50px;
        left: 20px;
        width: auto;
        img {
          width: 100px;
          margin-left: 0;
          margin-right: 0;
          opacity: 0;
          transition: 0.3s;
          .swiper-slide-active & {
            opacity: 1;
          }
        }
      }
    }
  }
}
////////////////////////////////////////////////
@keyframes productSlideFade {
  0% {
    transform: translateY(-5px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
//////////////////////////////////////////
.itemStyle_06 {
  @include fz_vw(14);
  line-height: normal;
  .update {
    margin-bottom: 0.5em;
    width: 6em;
  }
  .ttl {
    font-weight: normal;
    font-family: $font-ja;
  }
}
@media screen and (min-width: $break-point-middle) {
  .itemStyle_06 {
    font-size: 1.4rem;
    > * {
      display: flex;
      gap: 2em;
      .ttl {
        flex-grow: 1;
      }
    }
    .update {
      position: relative;
      top: 0.2em;
      margin-bottom: 0;
    }
    @media (any-hover: hover) {
      a:hover {
        transform: translateX(3px);
        opacity: 0.9;
        .ttl {
          text-decoration: underline;
        }
      }
    }
  }
}
