@charset "utf-8";
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.swiper-main {
  opacity: 0;
  &.swiper-initialized {
    opacity: 1;
  }
  .swiper-button-next:after,
  .swiper-button-prev:after {
    @include iconFont;
    @include fz_vw(14);
    font-weight: 400;
  }
}
////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
@media screen and (min-width: $break-point-middle) {
  .carouselStyle_01 {
    .swiper {
      overflow: visible;
    }
    .swiper-wrapper {
      transition-timing-function: linear;
    }
    .imgStyle {
      img {
        @include imgset;
      }
    }
    .swiper-slide {
      width: auto;
    }
    .imgStyle {
      img {
        height: $slider-h;
        width: auto;
        min-height: $slider-h-num + px;
      }
    }
    .swiperCtr {
      display: flex;
      width: 100%;
      font-family: $font-sub;
      font-size: 2.4rem;
      @include vwpxpc(padding-left, 40);
      position: absolute;
      bottom: -1.6em;
      width: 100%;
      .swiper-pagination {
        position: relative;
        left: auto;
        right: auto;
        height: auto;
        margin: 0;
        top: auto;
        color: $white;
        line-height: 1em;
        span {
          color: $black;
        }
        &::before {
          content: '';
          display: block;
          height: 1px;
          width: 38px;
          background-color: $key-color1;
          position: absolute;
          top: 0.8em;
          left: 0;
          transform-origin: 50% 50%;
          transform: rotate(-45deg);
        }
      }
      .swiper-pagination-current {
        position: relative;
        right: -0.2em;
      }
      .swiper-pagination-total {
        position: relative;
        left: -0.2em;
        top: 0.5em;
      }
      .swiper-pagination {
        width: auto;
      }
      .swiper-pagination-fraction {
        bottom: auto;
      }
    }
  }
}
////////////////////////////////////////////////////////////
.carouselStyle_01_sp {
  .swiper {
    overflow: visible;
  }
  .swiper-wrapper {
    transition-timing-function: linear;
  }
  .imgStyle {
    img {
      @include imgset;
    }
  }
  .swiperCtr {
    // display: flex;
    display: none;
    width: 100%;
    font-family: $font-sub;
    @include fz_vw(24);
    padding-left: $sp-pad;
    position: absolute;
    bottom: -1em;
    width: 100%;
    .swiper-pagination {
      position: relative;
      left: auto;
      right: auto;
      height: auto;
      margin: 0;
      top: auto;
      color: $white;
      line-height: 1em;
      span {
        color: $black;
      }
      &::before {
        content: '';
        display: block;
        height: 1px;
        width: 30 / $design-num-sp * 100vw;
        background-color: $key-color1;
        position: absolute;
        top: 0.8em;
        left: 0;
        transform-origin: 50% 50%;
        transform: rotate(-45deg);
      }
    }
    .swiper-pagination-current {
      position: relative;
      right: -0.1em;
    }
    .swiper-pagination-total {
      position: relative;
      left: -0.5em;
      top: 0.6em;
    }
    .swiper-pagination {
      width: auto;
    }
    .swiper-pagination-fraction {
      bottom: auto;
    }
  }
}
////////////////////////////////////////////////////////////
.carouselStyle_02 {
  .swiper {
    overflow: visible;
  }
}
////////////////////////////////////////////////////////////
.carouselStyle_03 {
  .swiper {
    overflow: visible;
  }
  .swiper-wrapper {
    transition-timing-function: linear;
  }
  .imgStyle {
    img {
      @include imgset;
    }
  }
  .swiper-slide {
    width: auto;
  }
  .imgStyle {
    img {
      height: 360 / $design-num-sp * 100vw;
      width: auto;
    }
  }
}
////////////////////////////////////////////////////////////
.carouselStyle_04 {
  .swiper {
    overflow: visible;
  }
  // .swiper-wrapper {
  //   transition-timing-function: linear;
  // }
  .imgStyle {
    img {
      @include imgset;
    }
  }
  .swiper-slide {
    transition: opacity 0.6s;
  }
  .swiper-slide-prev {
    opacity: 0;
  }
  .swiperCtr {
    display: flex;
    justify-content: center;
    width: 100%;
    font-family: $font-sub;
    @include fz_vw(24);
    position: absolute;
    bottom: -2.2em;
    left: -0.5em;
    .swiper-pagination {
      position: relative;
      left: auto;
      right: auto;
      height: auto;
      margin: 0;
      top: auto;
      color: $key-gray3;
      line-height: 1em;
      span {
        color: $black;
      }
      &::before {
        content: '';
        display: block;
        height: 1px;
        width: 35 / $design-num-sp * 100vw;
        background-color: $key-color1;
        position: absolute;
        top: 0.8em;
        left: -0.1em;
        transform-origin: 50% 50%;
        transform: rotate(-45deg);
      }
    }
    .swiper-pagination-current {
      position: relative;
      right: -0.1em;
    }
    .swiper-pagination-total {
      position: relative;
      left: 0.4em;
      top: 0.5em;
    }
    .swiper-pagination {
      width: auto;
    }
    .swiper-pagination-fraction {
      bottom: auto;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .carouselStyle_04 {
    .swiperCtr {
      justify-content: flex-start;
      font-size: 2.4rem;
      bottom: 0.5em;
      left: 0;
      .swiper-pagination {
        &::before {
          width: 38px;
          left: 0;
        }
      }
      .swiper-pagination-current {
        right: -0.2em;
      }
      .swiper-pagination-total {
        left: 0.7em;
        top: 0.5em;
      }
    }
  }
}
