@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
#mainVis {
  opacity: 0;
  .loaded & {
    animation: baseFade_01 1s 0.2s forwards;
  }
  > .inner {
    position: relative;
  }
  .mainVisLogo {
    z-index: 10;
    position: absolute;
    top: 433 / $design-num-sp * 100vw;
    left: 0;
    width: 100%;
    text-align: center;
    opacity: 0;
    transform: translateY(-10px);
    .loaded & {
      animation: baseFade_02 1s 0.4s forwards;
    }
    .col-graphic {
      img {
        width: 243 / $design-num-sp * 100vw;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .col-tx {
      margin-top: 14 / $design-num-sp * 100vw;
      @include fz_vw(14);
      line-height: 1em;
      letter-spacing: 0.3em;
      h1 {
        font-weight: 200;
      }
    }
  }
}
$slider-h-max: 610;
$slider-h-num: 475;
$slider-h: $slider-h-num/$slider-h-max * 100vh;
@media screen and (min-width: $break-point-middle) {
  #mainVis {
    overflow: clip;
    margin-bottom: 60px;
    > .inner {
      // height: 100vh;
      // min-height: $slider-h-max + px;
    }
    .mainVisLogo {
      top: 0;
      left: 0;
      height: $slider-h;
      min-height: $slider-h-num + px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      text-align: center;
      padding-bottom: 40px;
      // padding-top: 320px;
      // padding-bottom: 185/810 * 100vh;
      // padding-bottom: 100/810 * 100vh;
      .col-graphic {
        img {
          @include vwpxpc(width, 301);
          margin: 0;
        }
      }
      .col-tx {
        @include vwpxpc(margin-top, 16);
        font-size: 1.4rem;
      }
    }
  }
}
