///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.kvNav-product {
  position: relative;
  z-index: 10;
  margin-top: 70 / $design-num-sp * 100vw;
  .inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .item {
    opacity: 0;
    transform: translateY(-10px);
    &:nth-child(1) {
      .loaded & {
        animation: baseFade_02 1s 0.2s forwards;
      }
    }
    &:nth-child(2) {
      .loaded & {
        animation: baseFade_02 1s 0.3s forwards;
      }
    }
    &:nth-child(3) {
      .loaded & {
        animation: baseFade_02 1s 0.4s forwards;
      }
    }
    &:nth-child(4) {
      .loaded & {
        animation: baseFade_02 1s 0.5s forwards;
      }
    }
    span.space {
      display: none;
    }
    a {
      display: flex;
      align-items: center;
      flex-direction: column;
      text-align: center;
      .col-img {
        span {
          display: block;
          width: 70 / $design-num-sp * 100vw;
          margin-left: auto;
          margin-right: auto;
          &.over {
            display: none;
          }
        }
        img {
          // @include imgset;
          display: block;
          width: 100%;
          transition: 0.6s;
          @include lensShadow;
        }
      }
      .col-content {
        transition: 0.3s;
      }
      .enTx {
        font-family: $font-sub;
        line-height: (14/26) + em;
        @include fz_vw(26);
      }
      .jaTx {
        margin-top: 0.8em;
        line-height: 1em;
        @include fz_vw(8);
        font-weight: 200;
      }
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .kvNav-product {
    margin-top: -16px;
    .inner {
      justify-content: center;
      @include vwpxpc(gap, 45);
    }
    .item {
      a {
        flex-direction: row;
        text-align: left;
        gap: 3px;
        padding-top: 3px;
        .col-img {
          width: 80px;
          span {
            width: auto;
            margin: 0;
          }
        }
        .enTx {
          line-height: (16/30) + em;
          font-size: 3rem;
        }
        .jaTx {
          font-size: 1rem;
        }
      }
      a:hover {
        .col-img {
          img {
            transform: scale(1.1);
          }
        }
        .col-content {
          opacity: 0.8;
          transform: translateX(2px);
        }
      }
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.kvSpecList {
  border-top: 1px solid $key-gray2;
  padding-top: 10 / $design-num-sp * 100vw;
  margin-top: 16 / $design-num-sp * 100vw;
  ul {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    &::after {
      content: '';
      width: 100%;
    }
  }
  li {
    line-height: (18/12) + em;
    @include fz_vw(12);
    &::after {
      content: '/';
      padding: 0 0.5em;
      font-weight: 200;
    }
    &:last-child::after {
      display: none;
    }
    &.style_01 {
      &::after {
        display: none;
      }
    }
    &:nth-child(n + 4) {
      order: 1;
    }
    span {
      font-weight: 200;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .kvSpecList {
    padding-top: 12px;
    margin-top: 5px;
    ul {
      flex-wrap: nowrap;
      @include vwpxpc(gap, 90);
      &::after {
        display: none;
      }
    }
    li {
      font-size: 1.2rem;
      &::after {
        display: none;
      }
      &:nth-child(n + 4) {
        order: 0;
      }
    }
  }
}
