  .btnCol {
    margin-top: 56 / $design-num-sp * 100vw;
    display: flex;
  }
}
@media screen and (min-width: $break-point-middle) {
  .topAboutSec {
    .head {
      position: absolute;
      left: 0;
      top: 496px;
      width: 100%;
      z-index: 100;
      margin-bottom: 0;
      text-align: left;
    }
    .imgSec {
      ul {
        transform: translateX(100%);
        display: flex;
        justify-content: flex-end;
        gap: 14px;
        opacity: 0;
        img {
          @include imgset;
          height: 527px;
          width: auto;
        }
      }
    }
    .content {
      display: flex;
      justify-content: flex-end;
      padding-top: 50px;
      > .inner {
        max-width: 473px;
      }
    }
    .summary {
      font-size: 1.4rem;
      p {
        margin-bottom: (30/14) + em;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    .btnCol {
      margin-top: 44px;
    }
    &.is-active {
      .imgSec {
        ul {
          animation: aboutImgMove 2s $ease-out-quad forwards;
        }
      }
    }
  }
}
////////////////////////////////////////////////
@keyframes aboutImgMove {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0%);
    opacity: 1;
  }
}
