///////////////////////////////////////////////////////////
.producerSec {
  > .inner {
    position: relative;
  }
  .col-content {
    padding-top: 168 / $design-num-sp * 100vw;
    padding-bottom: 40 / $design-num-sp * 100vw;
    position: relative;
    &::before {
      content: '';
      width: 0vw;
      background-color: $key-gray3;
      position: absolute;
      top: 0;
      left: -100vw;
      bottom: 0;
      transition: 1s $ease-out-quad;
    }
    > .inner {
      position: relative;
    }
  }
  .ttl {
    font-weight: 500;
    @include fz_vw(12);
    letter-spacing: 0.03em;
    margin-bottom: 6 / $design-num-sp * 100vw;
  }
  .name {
    line-height: (26/36) + em;
    @include fz_vw(36);
    font-family: $font-dec;
    margin-bottom: 16 / $design-num-sp * 100vw;
  }
  .summary {
    font-family: $font-ja;
    line-height: (24/14) + em;
    @include fz_vw(14);
    text-align: justify;
    br {
      display: none;
    }
    p {
      font-weight: 400;
    }
  }
  .col-img {
    width: 1094/3 / $design-num-sp * 100vw;
    position: absolute;
    top: -150 / $design-num-sp * 100vw;
    right: -130 / $design-num-sp * 100vw;
    opacity: 0;
    transform: translateY(-10px);
    img {
      @include imgset;
    }
  }
  .imgStyle {
    position: relative;
    &.style_01 {
      position: absolute;
      top: 0;
      left: 0;
      rotate: 0deg;
      transition: rotate 0.3s;
    }
  }
  &.is-active {
    .col-content {
      &::before {
        width: 200vw;
      }
    }
    .col-img {
      animation: baseFade_02 1s 0.5s forwards;
    }
    .imgStyle {
      &.style_01 {
        animation: rotate_01 0.6s 1s forwards;
      }
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .producerSec {
    > .inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .col-content {
      padding-top: 40px;
      padding-bottom: 40px;
      // font-size: 1.2rem;
      // padding-left: 8em;
    }
    .ttl {
      font-size: 1.2rem;
      // padding-left: 9em;
      margin-bottom: 6px;
    }
    .name {
      line-height: (30/40) + em;
      font-size: 4rem;
      margin-bottom: 12px;
    }
    .summary {
      font-size: 1.4rem;
      br {
        display: block;
      }
    }
    .col-img {
      width: 467 / $w_04 * 100%;
      position: relative;
      top: auto;
      right: auto;
    }
  }
}
@media screen and (min-width: $break-point-design) {
  .producerSec {
    .col-content {
      font-size: 1.2rem;
      padding-left: 8em;
    }
  }
}
