.chartWrap {
  position: relative;
  .legend {
    position: absolute;
    .label {
      font-weight: 400;
      @include fz_vw(10);
    }
  }
  .legend-y {
    top: 0;
    left: -5 / $design-num-sp * 100vw;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10 / $design-num-sp * 100vw;
    .sep {
      display: block;
      height: 300 / $design-num-sp * 100vw;
      span {
        display: block;
        width: 1px;
        height: 100%;
        background: rgb(200, 178, 163);
        background: linear-gradient(0deg, rgba(200, 178, 163, 1) 0%, rgba(73, 40, 13, 1) 100%);
