      display: block;
      width: 190 / $design-num-sp * 100vw;
      span {
        display: block;
        width: 100%;
        height: 1px;
        background: rgb(200, 178, 163);
        background: linear-gradient(90deg, rgba(200, 178, 163, 1) 0%, rgba(73, 40, 13, 1) 100%);
      }
