@mixin bgRatio {
  &.style1x {
    display: block;
  }
  &.style2x,
  &.style3x {
    display: none;
  }
  @media screen and (-webkit-device-pixel-ratio: 2) {
    &.style2x {
      display: block;
    }
    &.style1x,
    &.style3x {
      display: none;
    }
  }
  @media screen and (-webkit-device-pixel-ratio: 3) {
    &.style3x {
      display: block;
    }
    &.style1x,
    &.style2x {
      display: none;
    }
  }
  @media screen and (min-width: $break-point-middle) {
    &.spElem {
      display: none;
    }
  }
}

