@keyframes slideOutLeft {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}
@keyframes slideOutRight {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
}
@keyframes slideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}
@keyframes slideOutDown {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}
// 第一引数で基準となる画面幅のフォントサイズ、第二引数で基準となる画面幅を入力
@function get_vw($size, $viewport: $design-num-sp) {
  $rate: 100 / $viewport;
  @return $rate * $size * 1vw;
}

@mixin fz_vw($font_size: 10) {
  font-size: $font_size * 1px;
  font-size: get_vw($font_size);
}

@mixin fz_vwc($font_size: 10) {
  font-size: get_vw($font_size);
  @media screen and (min-width: $break-point-middle) {
    font-size: $font_size / 10 + rem;
  }
}
////
@function get_vwpc($size, $viewport: $design-num) {
  $rate: 100 / $viewport;
  @return $rate * $size * 1vw;
}

@mixin fz_vwpc($font_size: 10) {
  font-size: $font_size * 1px;
  font-size: get_vwpc($font_size);
}

@mixin fz_vwpcmax($font_size: 10) {
  font-size: $font_size * 1px;
  font-size: get_vwpc($font_size);
  @media screen and (min-width: $design-width) {
    font-size: $font_size / 10 + rem;
  }
}

@mixin fz_vwpcmin($font_size: 10) {
  font-size: $font_size / 10 + rem;
  @media screen and (min-width: $break-point-design) {
    font-size: $font_size * 1px;
    font-size: get_vwpc($font_size);
  }
}

//clamp
@function clamp_vw($min, $size, $vp: $design-num) {
  $min: $min * 0.1rem;
  $max: $size * 0.1rem;
  $num: 100 / $vp * 1vw;
  $size_vw: $num * $size;
  @return clamp($min, $size_vw, $max);
}

//vw px
@mixin vwpx($prop, $size) {
  #{$prop}: $size/$design-num-sp * 100vw;
  @media screen and (min-width: $break-point-middle) {
    #{$prop}: #{$size}px;
  }
}

@mixin vwpxpc($prop, $size) {
  #{$prop}: $size/$design-num * 100vw;
  @media screen and (min-width: $break-point-design) {
    #{$prop}: #{$size}px;
  }
}

@mixin vwpxpc2($prop, $size, $view) {
  #{$prop}: $size/$view * 100vw;
  @media screen and (min-width: #{$view}px) {
    #{$prop}: #{$size}px;
  }
}

@mixin setbasepad() {
  padding-left: $base-pad-num-min + px;
  padding-right: $base-pad-num-min + px;
  @media screen and (min-width: $design-width) {
    padding-left: $base-pad;
    padding-right: $base-pad;
  }
}

//アローボタン
@mixin arrowBtn_01 {
  transition: 0.6s;
  display: inline-block;
  .label {
    display: flex;
    align-items: center;
    p {
      @include fz_vw(14);
      font-weight: 400;
      margin-right: 14 / $design-num-sp * 100vw;
      @media screen and (min-width: $break-point-middle) {
        // font-size: 1.4rem;
        @include fz_vwpcmin(14);
        margin-right: 8px;
      }
    }
  }
  .arrow {
    width: 60 / $design-num-sp * 100vw;
    height: 4 / $design-num-sp * 100vw;
    position: relative;
    transition: 0.6s;
    @media screen and (min-width: $break-point-middle) {
      width: 60px;
      height: 4px;
    }
    span {
      display: block;
      height: 1px;
      background-color: $white;
      &:first-child {
        margin-top: 3 / $design-num-sp * 100vw;
        @media screen and (min-width: $break-point-middle) {
          margin-top: 3px;
        }
      }
      &:last-child {
        width: 8 / $design-num-sp * 100vw;
        @media screen and (min-width: $break-point-middle) {
          width: 8px;
        }
        transform: rotate(24deg);
        position: absolute;
        transform-origin: 0% 0;
        top: 0;
        right: 0;
      }
    }
  }
}

//記事エフェクト
@mixin articleEffect_01 {
  opacity: 0;
  transform: translateX(20px);
  transition: 1s $ease_04;
  &:nth-child(2n),
  .swiper-slide:nth-child(2n) & {
    a {
      .eyecatch {
        > .inner {
          &::after {
            transform-origin: 0% 0%;
          }
        }
      }
    }
  }
  a {
    .eyecatch {
      overflow: hidden;
      position: relative;
      > .inner {
        position: relative;
        &::after {
          transition: opacity 0.6s $ease_04 0.9s, transform 0.9s $ease_04;
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          background-color: $black;
          display: block;
          width: 100%;
          transform: scale(1, 0);
          transform-origin: 0% 100%;
        }
      }
    }
    .imgStyle {
      position: relative;
      opacity: 0;
      transform: scale(1.07);
      transform-origin: 50% 50%;
      transition: transform 1s $ease_04 0.8s, opacity 1s $ease_04 0.6s;
    }
    .content {
      transition: 0.6s;
    }
  }
  .is-active & {
    transform: translateX(0);
    opacity: 1;
    a {
      .eyecatch {
        > .inner {
          &::after {
            opacity: 0;
            transform: scale(1, 1);
          }
        }
      }
      .imgStyle {
        opacity: 1;
        transform: scale(1);
      }
    }
    @media screen and (min-width: $break-point-middle) {
      a:hover {
        .eyecatch {
          .imgStyle {
            transform: scale(1.05);
            background-color: $white;
          }
        }
        .content {
          opacity: 0.8;
          transform: translateX(5px);
        }
      }
    }
  }
  .is-active-after & {
    a {
      .imgStyle {
        transition: 0.8s;
      }
    }
  }
}
