@mixin maskMove_01($size) {
  @keyframes maskMove_01_#{$size} {
    0% {
      clip-path: circle($size at right 30% bottom 45%);
    }
    25% {
      clip-path: circle($size at right 30% bottom 50%);
    }
    50% {
