@charset "utf-8";
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.headStyle_01 {
  display: flex;
  justify-content: center;
}
//////////////////////////////////
.headStyle_02 {
  display: flex;
  justify-content: center;
}
@media screen and (min-width: $break-point-middle) {
  .headStyle_02 {
    justify-content: flex-start;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.ttlStyle_01 {
  font-family: $font-dec;
  line-height: (30/34) + em;
  @include fz_vw(34);
  font-weight: 700;
  > span {
    &.style_01 {
      padding-left: 1.2em;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .ttlStyle_01 {
    line-height: (30/40) + em;
    font-size: 4rem;
    > span {
      &.style_01 {
        padding-left: 2.5em;
      }
    }
  }
}
/////////////////////////////
.ttlStyle_02 {
  line-height: (46/60) + em;
  @include fz_vw(60);
  font-family: $font-dec;
  span {
    font-weight: 400;
  }
}
@media screen and (min-width: $break-point-middle) {
  .ttlStyle_02 {
    line-height: (80/110) + em;
    font-size: 11rem;
  }
}
/////////////////////////////
.ttlStyle_03 {
  @include fz_vw(14);
  font-weight: 400;
}
@media screen and (min-width: $break-point-middle) {
  .ttlStyle_03 {
    font-size: 1.4rem;
  }
}
/////////////////////////////
.ttlStyle_04 {
  font-family: $font-dec;
  font-weight: 700;
  @include fz_vw(60);
  line-height: (50/60) + em;
  letter-spacing: -0.02em;
}
@media screen and (min-width: $break-point-middle) {
  .ttlStyle_04 {
    @include fz_vwpcmax(70);
    line-height: (70/70) + em;
  }
}
/////////////////////////////
.ttlStyle_05 {
  line-height: (16/12) + em;
  @include fz_vw(12);
  font-weight: 400;
  &::before {
    content: '●';
  }
}
@media screen and (min-width: $break-point-middle) {
  .ttlStyle_05 {
    line-height: (22/12) + em;
    font-size: 1.2rem;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.summaryStyle_01 {
  line-height: (26/14) + em;
  @include fz_vw(14);
  text-align: justify;
  p {
    font-weight: 400;
  }
}
@media screen and (min-width: $break-point-middle) {
  .summaryStyle_01 {
    line-height: (24/12) + em;
    font-size: 1.2rem;
  }
}
//////////////////////////////////////////
.summaryStyle_02 {
  line-height: (28/14) + em;
  @include fz_vw(14);
  p {
    font-weight: 400;
    margin-bottom: (32/14) + em;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .summaryStyle_02 {
    line-height: (34/14) + em;
    font-size: 1.4rem;
    p {
      margin-bottom: (40/14) + em;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
//////////////////////////////////////////
.summaryStyle_03 {
  font-family: $font-ja;
  line-height: (22/14) + em;
  @include fz_vw(14);
  text-align: justify;
  p {
    font-weight: 400;
    margin-bottom: 2em;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .summaryStyle_03 {
    font-size: 1.4rem;
  }
}
//////////////////////////////////////////
.summaryStyle_04 {
  line-height: (16/12) + em;
  @include fz_vw(12);
  text-align: justify;
  p {
    font-weight: 400;
  }
  li {
    font-weight: 400;
  }
}
@media screen and (min-width: $break-point-middle) {
  .summaryStyle_04 {
    line-height: (22/12) + em;
    font-size: 1.2rem;
  }
}
//////////////////////////////////////////
.summaryStyle_05 {
  line-height: (24/14) + em;
  @include fz_vw(14);
  text-align: justify;
  p {
    font-weight: 400;
  }
}
@media screen and (min-width: $break-point-middle) {
  .summaryStyle_05 {
    line-height: (24/14) + em;
    font-size: 1.4rem;
  }
}
//////////////////////////////////////////
.summaryStyle_06 {
  line-height: (28/14) + em;
  @include fz_vw(14);
}
@media screen and (min-width: $break-point-middle) {
  .summaryStyle_06 {
    font-size: 1.4rem;
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.listStyle_01 {
  li {
    font-weight: 400;
    position: relative;
    display: inline;
    &::before {
      content: '●';
    }
  }
}
@media screen and (min-width: $break-point-middle) {
  .listStyle_01 {
    li {
      display: block;
      font-weight: 400;
      position: relative;
      margin-bottom: 0.2rem;
      padding-left: 1em;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}
/////////////////////////////////////////
.listStyle_02 {
  @extend .listStyle_01;
  li {
    margin-bottom: 0;
    display: block;
    &::before {
      content: '・';
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.btnStyle_01 {
  @include btnsetting;
  background-color: $key-color1;
  display: flex;
  align-items: center;
  height: 50 / $design-num-sp * 100vw;
  border-radius: 100px;
  color: $white;
  justify-content: center;
  overflow: hidden;
  position: relative;
  .labelTx {
    position: relative;
    @include fz_vw(14);
    line-height: 1em;
    font-weight: 400;
    transition: 0.3s;
    display: inline-block;
  }
}
@media screen and (min-width: $break-point-middle) {
  .btnStyle_01 {
    height: 32px;
    &::before {
      position: absolute;
      top: 0;
      left: -5%;
      content: '';
      width: 110%;
      height: 350%;
      background: lighten($key-color1, 5%);
      transform-origin: left bottom;
      transform: skewY(-10deg) scale(1, 0);
      transition: transform 1s;
    }
    .labelTx {
      font-size: 1.2rem;
    }
    &:hover,
    &.active {
      .labelTx {
        transform: translateX(3px);
      }
      &::before {
        transform-origin: left top;
        transform: skewY(-10deg) scale(1, 1);
      }
    }
  }
}
//////////////////////////////////////
.btnStyle_02 {
  @extend .btnStyle_01;
  .labelTx {
    letter-spacing: 0.03em;
  }
}
@media screen and (min-width: $break-point-middle) {
  .btnStyle_02 {
    height: 48px;
  }
}
//////////////////////////////////////
.btnStyle_03 {
  padding-left: 1em;
  padding-right: 1em;
  position: relative;
  overflow: hidden;
  text-align: center;
  display: block;
  @include fz_vw(12);
  &:before {
    content: '';
    height: 1px;
    width: 100%;
    background-color: $key-color1;
    bottom: 0;
    left: 0;
    position: absolute;
    @include transform(translateX(0%));
  }
  .labelTx {
    display: inline-block;
    font-weight: 400;
    @include fz_vw(12);
    transition: 0.3s;
  }
}
@media screen and (min-width: $break-point-middle) {
  .btnStyle_03 {
    font-size: 1.2rem;
    .labelTx {
      font-size: 1.2rem;
    }
    &.style_01 {
      &:hover {
        .labelTx {
          transform: translateX(2px);
        }
        &::before {
          animation: linemove_02 0.8s cubic-bezier(0.58, 0.3, 0.005, 1) 0s 1;
        }
      }
    }
  }
}
//////////////////////////////////////
.btnStyle_04 {
  @extend .btnStyle_01;
  &.style_01 {
    background-color: $black;
  }
  .labelTx {
    letter-spacing: 0.03em;
  }
  &.is-inactive {
    cursor: default;
    pointer-events: none;
    opacity: 0.5;
  }
}
@media screen and (min-width: $break-point-middle) {
  .btnStyle_04 {
    height: 44px;
    &.style_01 {
      &::before {
        background: lighten($black, 5%);
      }
    }
    .labelTx {
      font-size: 1.4rem;
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.videoStyle_01 {
  aspect-ratio: 16/9;
  video,
  iframe {
    width: 100%;
    height: 100%;
    // height: 294 / $design-num-sp * 100vw;
    // height: 353 / $design-num-sp * 100vw;
    object-fit: cover;
  }
}
