///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
.linkStyle_01 {
  font-weight: 400;
}
@media screen and (min-width: $break-point-middle) {
  .linkStyle_01 {
    &:hover {
      opacity: 0.9;
      text-decoration: underline;
    }
  }
}
///////////////////////////////////////////////////////////
///////////////////////////////////////////////////////////
@media screen and (max-width: $break-point-middle-sp) {
  table,
  tbody,
  tr,
  th,
  td {
    display: block;
  }
  th,
  td {
    font-weight: 400;
    line-height: 1.6em;
  }
  tr {
    display: flex;
    height: 120 / $design-num-sp * 100vw;
    flex-direction: column;
    justify-content: center;
    position: relative;
    &::before {
      content: '';
      display: block;
      background-color: $key-gray4;
      height: 1px;
      width: 200vw;
      position: absolute;
      bottom: 0;
      left: -50vw;
    }
  }
  th {
    letter-spacing: 0.5em;
    @include fz_vw(12);
    margin-bottom: 0.5em;
  }
  td {
    @include fz_vw(14);
  }
}
@media screen and (min-width: $break-point-middle) {
  .tblStyle_01 {
    position: relative;
    &::before {
      content: '';
      display: block;
      background-color: $key-gray4;
      height: 1px;
      width: 200vw;
      position: absolute;
      top: 0;
      left: -50vw;
    }
    th,
    td {
      padding-top: 30px;
      padding-bottom: 30px;
      font-weight: 400;
    }
    th {
      width: 200px;
      letter-spacing: 0.5em;
      position: relative;
      font-size: 1.2rem;
      &::before {
        content: '';
        display: block;
        background-color: $key-gray4;
        height: 1px;
        width: 200vw;
        position: absolute;
        bottom: 0;
        left: -50vw;
      }
    }
    td {
      font-size: 1.4rem;
    }
  }
}
